<template>
  <div class="info">
    <HeadTop :lang="lang" @changeAppLocale="changeAppLocale" :name="this.$t('message.f_15')+'数据'" />



    <div class="cent">
      <div class="ming_bix">
        <van-row type="flex" justify="space-between" class="">
          <van-col span="24"><span>可领取个人收益（USDT）</span>
            <h3>{{ hmdInfo ? hmdInfo.info[10] : "0" | figure }} <br /> <small>≈
                {{ hmdInfo ? ((Number(hmdInfo.info[10]))* Number(hmdInfo.fivePrice)) : "0" | figure }} FAN</small></h3>
          </van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="">
          <van-col span="24"><span>可领取节点收益（USDT）</span>
            <h3>{{ hmdInfo ? hmdInfo.info[6] : "0" | figure }} <br /> <small>≈
                {{ hmdInfo ? ((Number(hmdInfo.info[6])) * Number(hmdInfo.fivePrice)) : "0" | figure }} FAN</small></h3>
          </van-col>
        </van-row>
        <button class="btn" @click="getReward">
          {{ $t("message.f_28") }}
        </button>
        <!-- <button class="btn_no" >
                {{ $t("message.f_28") }}
              </button> -->
      </div>

      <div class="ming">
        <div class="my">
          <van-row type="flex" justify="space-between" gutter="20">
            <van-col span="12">
              <div class="box">
                <span><img src="../../assets/img/fan/icon1.png" alt="" width="20">购买总额</span>
                <h3>{{ hmdInfo ? hmdInfo.info[2] : "0" | figure }} USDT</h3>
              </div>

            </van-col>
            <van-col span="12">
              <div class="box">
                <span><img src="../../assets/img/fan/icon2.png" alt="" width="20">节点总业绩</span>
                <h3>{{ hmdInfo ? hmdInfo.info[23] : "0" | figure }} USDT</h3>
              </div>

            </van-col>
            <van-col span="12">
              <div class="box">
                <span><img src="../../assets/img/fan/icon3.png" alt="" width="20">个人销毁总量</span>
                <h3>{{ hmdInfo ? (Number(hmdInfo.info[18])) : "0" | figure }} FAN<br />
                 <!-- <small>≈{{ hmdInfo ? ((Number(hmdInfo.info[18])) / Number(hmdInfo.fivePrice)) : "0" | figure }}USDT</small> -->
                </h3>
              </div>

            </van-col>
            <van-col span="12">
              <div class="box">
                <span><img src="../../assets/img/fan/icon4.png" alt="" width="20">累计总收益</span>
                <h3>{{ hmdInfo ? (Number(hmdInfo.info[4])+Number(hmdInfo.info[5])+Number(hmdInfo.info[34])) : "0" |
                    figure
                }} USDT<br /> 
                  <!-- <small>≈{{ hmdInfo ? ( ( Number(hmdInfo.info[4])+Number(hmdInfo.info[5])+Number(hmdInfo.info[34])) * Number(hmdInfo.fivePrice)) : "0" | figure}}  -->
                    <!-- {{ hmdInfo ? (Number(hmdInfo.info[17])) : "0" | figure }} FAN</small> -->
                   </h3>
              </div>

            </van-col>
          </van-row>
        </div>


        <div class="list">
          <van-row type="flex" justify="space-between" class="line">
            <van-col span="12" class="textLeft"><span>我的累计购买量</span></van-col>
            <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[1] : "0" | figure }} USDT</span>
            </van-col>
          </van-row>
          <van-row type="flex" justify="space-between" class="line">
            <van-col span="12" class="textLeft"><span>个人总收益</span></van-col>
            <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[4] : "0" | figure }} USDT</span>
            </van-col>
          </van-row>
          <!-- <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"
                ><span>全球累计分红收益</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span>{{ hmdInfo ? hmdInfo.info[34] : "0"  | figure }} USDT</span></van-col
              >
            </van-row> -->
          <van-row type="flex" justify="space-between" class="line">
            <van-col span="12" class="textLeft"><span>节点总收益</span></van-col>
            <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[5] : "0" | figure }} USDT</span>
            </van-col>
          </van-row>
          <van-row type="flex" justify="space-between" class="line">
            <van-col span="12" class="textLeft"><span>全球分红总收益</span></van-col>
            <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[34] : "0" | figure }} USDT</span>
            </van-col>
          </van-row>
          <van-row type="flex" justify="space-between" class="line">
            <van-col span="12" class="textLeft"><span>购买次数</span></van-col>
            <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[13] : "0" }} </span></van-col>
          </van-row>
          <van-row type="flex" justify="space-between" class="line">
            <van-col span="12" class="textLeft"><span>出局次数</span></van-col>
            <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[12] : "0" }} </span></van-col>
          </van-row>
          <van-row type="flex" justify="space-between" class="line">
            <van-col span="12" class="textLeft"><span>我的邀请链接</span></van-col>
            <van-col span="12" class="textRight"><span>{{ link }}</span></van-col>
          </van-row>
        </div>
        <button class="btn"
         v-clipboard:copy="link"
                v-clipboard:success="onCopy"
                v-clipboard:error="onError"
        >复制邀请链接 </button>
      </div>

    </div>
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      //   currency: "",
      link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      timer: null,
      timer2: null,
      TokenPrice: 0,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account",]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    getUsdt(num) {
      return num * this.hmdInfo.fivePrice;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + "#/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    getPrice() {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      this.mxtContract.methods
        .getTokenPrice(
          "0x0496c8f51b6e6500bdd6e3bc0664857e976c6bc6",
          "0x6e42eae3317a19eb4b5115b0bce388bbf21cb917",
          "1000000000000000000"
        )
        .call()
        .then((res) => {
          // console.log('shdjsh')
          this.TokenPrice = parseFloat(
            web3.utils.fromWei(res, "ether")
          ).toFixed(4);
          console.log("getTokenPrice======", web3.utils.fromWei(res, "ether"));
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getReward: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.mxtContract.methods
        .withdrawProfit()
        .send({ from: account })
        .then((res) => {
          console.log("withdrawProfit", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },

 
  
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    this.currency = "HMD";
    if (this.$route.query.r) {
      localStorage.setItem("r", this.$route.query.r);
    }
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      // this.add_aesCoinContract();
    };
    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          _this.dataInfo = _this.hmdInfo.info;
          // _this.getPrice();
          if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            var invite = _this.hmdInfo.inviteCode || _this.hmdInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 10000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  background: linear-gradient(104deg, #0f1d28, #03030D, #03030D);
  padding-top: 60px;

  .top {
    text-align: center;
    padding: 30px;
    width: 370px;
    height: 188px;
    box-sizing: border-box;
    background: url(../../assets/img/info/top_bg.png) top center no-repeat;
    background-size: 100%;
    margin: 5px auto 20px;
    box-sizing: border-box;

    .sl {
      margin-bottom: 10px;
    }

    span {
      color: #eee;
      font-family: PingFangSC-Regular, PingFang SC;
      font-size: 12px;
    }

    h2 {
      font-size: 18px;
      margin-top: 4px;
      margin-bottom: 12px;
      font-family: DINAlternate-Bold, DINAlternate;
      font-weight: bold;
      color: #ffffff;
    }
  }

  .title {
    font-size: 26px;
    font-family: SourceHanSansCN-Bold, SourceHanSansCN;
    font-weight: bold;
    color: #4da9db;
    line-height: 39px;
    margin: 0 0 30px;

    span {
      color: #ffa0ae !important;
      font-size: 26px !important;
      font-weight: bold !important;
    }
  }

  .cent {
    // background: url(../../assets/img/info/bg.png) top center no-repeat,
    //   url(../../assets/img/3.png) bottom center no-repeat;
    // background-size: 100%, 100%;
    padding: 20px 0 50px;

    .ming {
      width: 92%;
      margin: 30px auto;
      padding: 20px 0 50px;
      position: relative;
      background: #141634;
      border-radius: 15px;

      .btn {
        width: 60%;
        margin: 30px auto 0;
        display: block;
        background: #1FC7D4;
        border: none;
        text-align: center;
        color: #fff;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        font-size: 17px;
        border-radius: 30px;
        padding: 15px;
      }

      .list {
        // background: #fff;
        border-radius: 0 0 15px 15px;
        // padding: 20px;
        width: 90%;
        margin: 0 auto 20px;
        box-sizing: border-box;

        .line {
          background: #0C0D1F;
          border-radius: 10px;
          margin: 10px 0;
          padding: 5px 10px;
        }

        span {
          display: inline-block;
          padding: 6px 0;
          color: #A6B2DC;
        }

        .textRight span {
          font-size: 15px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 600;
          color: #4da9db;
          overflow: hidden;
          display: block;
          width: 100%;
          text-align: right;
          text-overflow: ellipsis;
        }

      }
    }

    .my {
      margin: 0 auto 20px;
      padding: 0 10px;

      .box {
        background: url(../../assets/img/fan/4.png) top center no-repeat;
        background-size: 100% 100%;
        margin: 10px 0;
        padding: 15px 10px;
        text-align: center;

        span {
          color: #fff;

          img {
            margin-right: 5px;
            vertical-align: middle;
          }
        }

        h3 {
          text-align: center;
          color: #02FEF5;
          margin: 5px 0 0 0;
        }
      }
    }

    .ming_bix {
      background: url(../../assets/img/fan/info_bg.png) top center no-repeat;
      background-size: 100% 100%;
      width: 100%;
      margin: 0 auto;
      padding: 17px 40px 40px;
      box-sizing: border-box;

      span {
        color: #89B5FF;
        display: block;
        margin-top: 23px;
      }

      h3 {
        font-family: DINPro;
        font-weight: bold;
        color: #FFFFFF;
        font-size: 26px;
        line-height: 24px;
        margin-top: 10px;

        small {
          color: #2DF0AE;
          font-size: 15px;
        }
      }

      .btn {
        width: 120px;
        background: url(../../assets/img/fan/btn.png) top center no-repeat;
        background-size: 100%;
        border-radius: 30px;
        margin: 5px auto 0;
        display: block;
        border: none;
        text-align: center;
        color: #fff;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        font-size: 16px;
        padding: 10px;
      }

      // .btn_no{
      //    background: linear-gradient(#c9d0d1, #79797a);
      //   box-shadow: 0 3px 6px #bec1c7;
      //   border-radius: 30px;
      //   margin: 5px auto 0;
      //   display: inline-block;
      //   border: none;
      //   text-align: center;
      //   color: #fff;
      //   font-family: PingFangSC-Medium, PingFang SC;
      //   font-weight: 500;
      //   font-size: 18px;
      //   padding: 6px 20px;
      // }
    }
  }
}
</style>



