// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueI18n from 'vue-i18n'
import * as filters from './filters/filters.js'


import LangEn from '../static/lang/en'
import LangZhCHS from '../static/lang/zhCHS'
import LangZhCHT from '../static/lang/zhCHT'


import store from "./store";

import Vant from 'vant';
import 'vant/lib/index.css';

  


import VueClipboard from 'vue-clipboard2';

Vue.use(VueClipboard)
Vue.use(Vant);
Vue.use(Vant);
Vue.prototype.ArrList={
    dappArr:"0xfa2b54b509ca5b2e36ddd81879b189ce9e259e21",
    UsdtArr:"0x5bb906F094cffF3141eA31c20AC7b966026e6Fb8",
    FiveArr:"0x7169fA366ED2D053F34d24A8E0C73C1C9595Cf27",
    GtArr:"0x1FA2f1562e4e652F69087D546E04bBaC53d9856A",
    AesArr:"0x6485accb893eac4f4de15b6ea7c1192f96db88ca",
    XhArr:"0x000000000000000000000000000000000000dead",
    AesLpArr:"0x3de5ecdeb11eefd8651d4c7259c4de9627a06280",//NFT
}

Vue.config.productionTip = false;

Object.keys(filters).forEach(key=>{
    Vue.filter(key,filters[key])//插入过滤器名和对应方法
})

Vue.use(VueI18n);



const i18n = new VueI18n({
    locale: 'zhCHS', // 语言标识
    messages: {
        'en': LangEn,
        'zhCHS': LangZhCHS,
        'zhCHT': LangZhCHT
    }
});



/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    i18n,
    template: '<App/>',
    components: { App }
});


