<template>
  <div class="info">
    <HeadTop :lang="lang" @changeAppLocale="changeAppLocale" :page1="'bonus'" :name="'NFT卡牌'" />
      <div class="line_box">
         <van-row type="flex" justify="space-between" gutter="20">
            <van-col span="24" class="textCenter">
            <span>NFT总分红(USDT)</span>
            <h3  class="textCenter">{{ hmdInfo ? Number(hmdInfo.info[40]) : "0" | figure }} </h3>
          </van-col>
          <van-col span="12"  class="textCenter">
            <span>NFT发售总量</span>
            <h3  class="textCenter">10,000</h3>
          </van-col>
          <van-col span="12"  class="textCenter">
            <span>已发售数量</span>
            <h3  class="textCenter">2,000 </h3>
          </van-col>

        </van-row>
      </div>
       <van-row type="flex" justify="space-between" gutter="20">
         <van-col span="12" class="textRight">
              <button class="btn" >NFT交易 </button>
         </van-col>
          <van-col span="12" class="textLeft">
              <button class="btn" >NFT转账 </button>
         </van-col>
       </van-row>
 <h1 class="textLeft title">我的<span>NFT卡牌</span> <small>(共:{{nftInfo?nftInfo.balanceOf:'0'}}张NFT)</small></h1>
    <div class="cent" v-if="list.length>0">
        <van-row type="flex" justify="space-between" gutter="15">
          
          <van-col span="12" class="textLeft" v-for="(itme,index) in list" :key="index">
            <div class="list">
              <div class="line">
                <img src="../../assets/img/fan/nft.png" alt="" width="100%">
            </div>
            <h1># {{itme}}</h1>
            </div>
          </van-col>

        </van-row>

    </div>
    <div v-else class="textCenter note">
         <img src="../../assets/img/2.png" alt="" width="30%">
         <p>还没有NFT哦~</p>
    </div>
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      tabsNum: 1,
    list:[],
      //   currency: "",
      //   link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      //   timer: null,
      //   timer2: null,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account","nftInfo","nftContract"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract","add_nftContract"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
       //查询nft
    getNFTFun: function () {
      
      if (this.nftContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      console.log("getNFTFun", 1);
      var _this = this;
      var account = _this.account;
      // if (vipAvgBonus <= 0) {
      //   this.$toast(this.$t("message.bonusMsg"));
      //   return;
      // }
      // this.$toast.loading({
      //   // message: this.$t("message.pack"),
      //   duration: 0,
      //   overlay: true,
      //   closeOnClickOverlay: true,
      // });
      this.nftContract.methods
        .tokenIdsOf(account,0,10000)
        .call({ from: account })
        .then((res) => {
          console.log("getNFTFun", res);
          _this.$toast.clear();
         this.list = res
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    //领取分红奖励
    getvipDividendFun: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      // if (vipAvgBonus <= 0) {
      //   this.$toast(this.$t("message.bonusMsg"));
      //   return;
      // }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .getBonusReward()
        .send({ from: account })
        .then((res) => {
          console.log("getvipDividendFun", res);
          // if (res.status) {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          // _this.getCoinBalance();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      this.add_nftContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract || !this.nftInfo) {
          getData();
        } else {
            this.getNFTFun();
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  .van-count-down {
    color: #75a9db;
  }
background: linear-gradient(104deg,#0f1d28,#03030D,#03030D);
padding-top:60px;
  .title{
     font-size: 22px;
    font-family: SourceHanSansCN-Bold, SourceHanSansCN;
    font-weight: bold;
    color: #01BAE9;
    line-height: 39px;
    margin: 30px auto 0;
    width: 88%;
    span{
      color: #fff;
    }
    small{
      font-weight: normal;
      font-size: 16px;
      color: #ff8917;
    }
  }
  .line_box {
    width:92%;
    margin: 30px auto 20px;
    background: url(../../assets/img/fan/min_bg.png) top center no-repeat;
    background-size: 100% 100%;
    position: relative;
    padding: 25px 20px 10px;
    box-sizing: border-box;

    span {
      font-weight: 600;
      color: #fff;
     
    }

    .bt {
      font-size: 12px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #000;
      margin-bottom: 10px;
    }

    h3 {
      font-size: 20px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: bold;
      color: #241c3e;
       margin-bottom: 10px;
          margin-top:8px;
    }

    h4 {
      font-size: 14px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #fff;
    }
  }

  .cent {
    width: 92%;
    margin: 10px auto;
    // padding: 20px 0 50px;
    position: relative;
    // background: #141634;
    border-radius: 15px;
  }

}
  .btn{
    width: 150px;
    // margin: 0 auto;
    // display: block;
      background: url(../../assets/img/fan/btn2.png) top center no-repeat;
    background-size: 100% ;
  border: none;
  text-align: center;
  color:#fff;
  font-family: PingFangSC-Medium, PingFang SC;
font-weight: 500;
font-size: 17px;
// border-radius: 10px;
padding:12px 22px;
  }

.list {
  background: #141634;
  border-radius: 0 0 15px 15px;
  padding:10px 10px 15px;
   border-radius: 10px;
  box-sizing: border-box;
  margin-bottom: 15px;
  .line{
    background: #0C0D1F;
    border-radius: 10px;
       padding: 10px;
    // margin:10px 0;
    // padding: 5px 10px;
  }
  h1{
    font-size: 18px;
    padding: 15px 5px 10px 5px;
      color: #fff;
      text-align: center;
  }
  span {
    display: inline-block;
    // padding: 6px 0;
    color: #A6B2DC;
    float: right;
  }

  h2 {
    font-size: 12px;
    color: #d3d3d3;
     font-weight: normal;
     text-align: right;
         padding:0 10px;
  }
}
.note{
  margin-top:40px;
  p{color: #888;text-align: center;font-size: 14px;margin-top: 10px;}
}
</style>



