<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "en",
      currency: "",
      link: "--",
      coinBalance: 0,
      isApprove: false,
      isFiveApprove: false,
      stakeAmount: null,
       stakeAmount2: null,
      withdrawAmount: "",
      timer: null,
      timer2: null,
      show: false,
      isHome: true,
      nbn: 0,
      isApproveGrcc: false,
      cionType: 'five',
      isOpen: true,
      tab: 1
    };
  },
  computed: {
    ...mapState("main", [
      // "priceHMD",
      // "priceCNTM",
      "hmdInfo",
      "account",
      "balance",
      "mxtCoinContract",
      "mxtContract",
      "hmdBalance",
      "FiveCoinContract",
      "nftInfo",
      // "FiveDead",
    ]),
  },
  components: {
    HeadTop,
  },
  methods: {
    ...mapActions("main", [
      // "add_price",
      "add_hmdInfo",
      "add_mxtCoinContract",
      "add_hmdCoinContract",
      "add_nftContract"
    ]),
    changeCion(type) {
      this.tab = type;

    },
    showPop: function (arg, callback) {
      if (arg == "show") {
        this.show = true;
      }
    },
    pirceInput(type) {
      if (type == 1) {
        let str = "" + this.stakeAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.stakeAmount = +str2;
        }
      } else {
        let str = "" + this.withdrawAmount;
        if (str.indexOf(".") !== -1) {
          let arr = str.split("");
          arr.splice(arr.length - 1);
          let str2 = arr.join("");
          this.withdrawAmount = +str2;
        }
      }
    },
    protectAddress: function (addr, num) {
      if (addr) {
        var newAddr = addr.replace('"', "").replace('"', "");
        if (newAddr == "0x0000000000000000000000000000000000000000") {
          return "--";
        }
        if (!newAddr) {
          return "--";
        }
        if (newAddr == 1) {
          return newAddr;
        }
        return (newAddr + "").length == 42
          ? newAddr.substring(0, num) + "****" + newAddr.substring(39, 42)
          : newAddr;
      }
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    ceilNum(d, prex = 4) {
      if (parseInt(d) == d) return d;
      let len = 1;
      let i = 0;
      while (parseInt(d) != d) {
        d = d * 10;
        len *= 10;
        i++;
      }
      let last = Math.abs(i <= prex ? 0 : d % (Math.pow(10, (i - prex))));
      let d1 = d - last;
      if (last > 0) {
        d1 = d1 + Math.pow(10, (i - prex));
      }
      d1 = d1 / len;
      return d1;
    },
    getNbn(e, type) {
      if (type == 1) {
        var val = e;
      } else {
        var val = (e.target.value);
      }
      if (!e) return;
      var Price2 = this.hmdInfo.fivePrice;
      var prex = 4;
      var price = Number(val) * 0.3 * Number(Price2);

      // info.fivePrice =null;
      if (parseInt(price) == price) {
        this.nbn = price;
        return
      }
      let len = 1;
      let i = 0;
      while (parseInt(price) != price) {
        price = price * 10;
        len *= 10;
        i++;
      }
      let last = Math.abs(i <= prex ? 0 : price % (Math.pow(10, (i - prex))));
      let d1 = price - last;
      if (last > 0) {
        d1 = d1 + Math.pow(10, (i - prex));
      }
      d1 = d1 / len;
      this.nbn = d1;
    },
    //取消抵押
    withdraw: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      // var myzy = web3.utils.fromWei(_this.hmdInfo.info[19], "ether");
      if (myzy <= 0) {
        _this.$toast(_this.$t("message.noZy"));
        return;
      }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .exit()
        .send({ from: account })
        .then((res) => {
          console.log("withdraw", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          // }

          _this.withdrawAmount = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    stake: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var inviteCode = _this.hmdInfo.inviteCode.toString();
      var referrer = _this.hmdInfo.referrer.toString();
      var account = _this.account;

      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }

      if (!referrer || referrer == "--") {
        referrer = _this.$route.query.invite ? _this.$route.query.invite.toString() : 1;
      }
      if (!inviteCode || inviteCode == "--") {
        inviteCode = _this.account;
      }

      if (inviteCode == referrer) {
        referrer = "1";
      }
      if ((Number(_this.stakeAmount) % 100) > 0) {
        _this.$toast(_this.$t("message.yu"));
        return;
      }
      if (Number(_this.stakeAmount) > Number(_this.coinBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }


      if (Number(_this.balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      if (Number(_this.stakeAmount) < 100) {
        _this.$toast(_this.$t("message.f_67"));
        return;
      }
      if (Number(_this.stakeAmount) > 5000) {
        _this.$toast('最多可投5000H/s');
        return;
      }

      if (!referrer || referrer == "--") {
        _this.$toast(_this.$t("message.onReferees"));
        return;
      }
      var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .investInByToken(inviteCode, referrer, web3.utils.toWei(_this.stakeAmount.toString(), 'ether'))
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          //   setTimeout(function(){
          //      _this.add_hmdInfo();
          //     _this.getCoinBalance();
          //     _this.add_hmdCoinContract();
          //   },10000)
          // }

          _this.stakeAmount = '';
          _this.nbn = 0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    stake2: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var inviteCode = _this.hmdInfo.inviteCode.toString();
      var referrer = _this.hmdInfo.referrer.toString();
      var account = _this.account;

      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }

      if (!referrer || referrer == "--") {
        referrer = _this.$route.query.invite ? _this.$route.query.invite.toString() : 1;
      }
      if (!inviteCode || inviteCode == "--") {
        inviteCode = _this.account;
      }

      if (inviteCode == referrer) {
        referrer = "1";
      }
      if ((Number(_this.stakeAmount2) % 100) > 0) {
        _this.$toast(_this.$t("message.yu"));
        return;
      }
      if (Number(_this.stakeAmount2) > Number(_this.coinBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }


      if (Number(_this.balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount2) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      if (Number(_this.stakeAmount2) < 100) {
        _this.$toast(_this.$t("message.f_67"));
        return;
      }
      if (Number(_this.stakeAmount2) > 5000) {
        _this.$toast('最多可投5000H/s');
        return;
      }

      if (!referrer || referrer == "--") {
        _this.$toast(_this.$t("message.onReferees"));
        return;
      }
      var monye = (_this.stakeAmount2 * 10 ** 18).toString();
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .investIn(inviteCode, referrer, web3.utils.toWei(_this.stakeAmount2.toString(), 'ether'))
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
          //   setTimeout(function(){
          //      _this.add_hmdInfo();
          //     _this.getCoinBalance();
          //     _this.add_hmdCoinContract();
          //   },10000)
          // }

          _this.stakeAmount2 = '';
          _this.nbn = 0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);

      // this.$router.push({
      //   name: this.$route.name,
      //   params: { lang: this.lang },
      //   query: { r: localStorage.getItem("r") },
      // });
    },

    // Five授权
    FiveApprove: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.FiveCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isFiveApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    FiveisApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.FiveCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isFiveApprove = true;
          }
        });
    },

    // USDT授权
    lpApprove: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.hmdInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
