          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getUrl: function () {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + hash;
      var newAddr = "";
      if (this.hmdInfo) {
        if (this.hmdInfo.referrer) {
          var addr = this.hmdInfo.inviteCode;
          newAddr = addr.replace('"', "").replace('"', "");
          this.link = url + "?r=" + newAddr;
        } else {
          this.link = url;
        }
      }
      return this.link;
    },
    // 是否授权
    isApproveFun: function () {
      var account = this.account;
      var address = this.hmdInfo.address;

      this.mxtCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log('usdt=========isApproveFun', res)
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
    //获取当前币种余额
    getCoinBalance: function () {
      if (this.mxtCoinContract) {
        var account = this.account;
        this.mxtCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = num;
          });
      }
    },
    get_param: function (name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      // console.log(r)
      return null;
    },
  },
  created() {
    this.add_mxtCoinContract();
    this.getCoinBalance();
  },
  mounted() {
    var list = ['0xd4636b5b9D146C8FC4CE1522442925cCCD6dE029', '0x7a384eE5C45fe2a11a12499be06B750Fb680Ef55', '0xbE76a223e0747D2f5DE66009FedBff93D6Ced646', '0x69A8618CFD0cd6957915E821E58c54eC053ca64A']
    //  var isNameList=false;
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "en");
    if (this.$route.query.invite) {
      localStorage.setItem("invite", this.$route.query.invite);
    }

    var getData = () => {
      _this.add_hmdInfo();
      _this.add_mxtCoinContract();
      _this.add_hmdCoinContract();
      _this.add_nftContract();
    };

    var getDataCalculate = () => {
      getData();
      _this.timer = setInterval(() => {
        if (!_this.hmdInfo || !_this.mxtCoinContract || !_this.hmdBalance || !_this.nftInfo) {
          getData();
        } else {
          //  list.forEach(element => {
          //   console.log(_this.account.toUpperCase()==element.toUpperCase())
          //   if(_this.account.toUpperCase()==element.toUpperCase()){
          //      if(_this.nftInfo.balanceOf>0){
          //           _this.isOpen=true
          //         }
          //   }
          // });

          _this.getCoinBalance();
          _this.isApproveFun();
          _this.FiveisApproveFun();
          clearInterval(_this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    _this.timer2 = setInterval(() => {
      if (_this.timer) {
        clearInterval(_this.timer);
        _this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

