<template>
  <div class="info">
    <HeadTop :lang="lang" @changeAppLocale="changeAppLocale" :page1="'bonus'" :name="'节点数据'" />
      <div class="line_box">
        <span v-if="hmdInfo && hmdInfo.info[7] == 0"> {{ $t("message.f_61") }}</span>
        <span v-if="hmdInfo && hmdInfo.info[7] == 1"> {{ $t("message.f_61_2") }}</span>
        <span v-if="hmdInfo && hmdInfo.info[7] == 2"> {{ $t("message.f_61_3") }}</span>
        <span v-if="hmdInfo && hmdInfo.info[7] == 3"> {{ $t("message.f_61_4") }}</span>
        <span v-if="hmdInfo && hmdInfo.info[7] == 4"> {{ $t("message.f_61_5") }}</span>
        <div class="bt"> 可领取全球分红奖励</div>
        <h2>{{ hmdInfo ? hmdInfo.info[32] : "0" | figure }} USDT</h2>
        <!-- <h4>≈{{ hmdInfo ? (Number(hmdInfo.info[32])) : "0" | figure }}USDT</h4> -->
      </div>
      <button class="btn" @click="getvipDividendFun" >立即领取 </button>

    <div class="cent">


      <div class="my">
        <van-row type="flex" justify="space-between" gutter="20">
            <!-- <van-col span="12">
            <span>全球总分红</span>
            <h3>{{ hmdInfo ? Number(hmdInfo.info[34]) : "0" | figure }} </h3>
          </van-col> -->
          <van-col span="24" class="textCenter">
            <span class="textCenter">今日全球分红（USDT）</span>
            <h3 class="textCenter"> {{ hmdInfo ? Number(hmdInfo.info[35]) : "0"  | figure}} </h3>
          </van-col>
          <van-col span="12">
            <span class="textCenter">累计全球分红(USDT)</span>
            <h3 class="textCenter">{{ hmdInfo ? Number(hmdInfo.info[36]) : "0" | figure }}</h3>
          </van-col>
          <van-col span="12">
            <span class="textCenter">分红总人数</span>
            <h3 class="textCenter">{{ hmdInfo ? Number(hmdInfo.info[37]) : "0" }} </h3>
          </van-col>

        </van-row>
      </div>
      <div class="list">
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>我的节点等级</span></van-col>
          <van-col span="12" class="textRight"><span>V {{ hmdInfo ? hmdInfo.info[7] : "0" }}</span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>有效地址</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[25] : "0" }} </span>
          </van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>邀请地址</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[11] : "0" }} </span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>邀请业绩</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[21]  : "0"  | figure}} USDT</span></van-col>
        </van-row>
          <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>节点总地址</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[20] : "0" }} </span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>节点总业绩</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[23]  : "0"  | figure}} USDT</span></van-col>
        </van-row>
          <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>初级节点V1</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[26] : "0" }} </span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>中级节点V2</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[27] : "0" }} </span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>高级节点V3</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[28] : "0" }} </span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>超级节点V4</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[29] : "0" }} </span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>特级节点V5</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[30] : "0" }} </span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>全网分红倒计时</span></van-col>
          <van-col span="12" class="textRight" v-if="hmdInfo"><span> <van-count-down :time="hmdInfo.info[15] ? hmdInfo.info[15]*1000 : 0" /></span></van-col>
        </van-row>
      </div>
    </div>
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      tabsNum: 1,
      F5data: {
        allBonus: 0,
        balanceBonus: 0,
        vipLength: 0,
      },
      //   currency: "",
      //   link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      //   timer: null,
      //   timer2: null,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    //领取分红奖励
    getvipDividendFun: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      // if (vipAvgBonus <= 0) {
      //   this.$toast(this.$t("message.bonusMsg"));
      //   return;
      // }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .getBonusReward()
        .send({ from: account })
        .then((res) => {
          console.log("getvipDividendFun", res);
          // if (res.status) {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          // _this.getCoinBalance();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          // this.getMiningInfoFun(1);
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  .van-count-down {
    color: #75a9db;
  }
background: linear-gradient(104deg,#0f1d28,#03030D,#03030D);
padding-top:60px;

  .title {
    font-size: 26px;
    font-family: SourceHanSansCN-Bold, SourceHanSansCN;
    font-weight: bold;
    color: #4da9db;
    line-height: 39px;
    margin: 0 0 15px;

    span {
      color: #ffa0ae !important;
      font-size: 26px !important;
      font-weight: bold !important;
    }
  }
  .line_box {
    width:92%;
    margin: 30px auto 20px;
    background: url(../../assets/img/fan/min_bg.png) top center no-repeat;
    background-size: 100% 100%;
    position: relative;
    padding: 25px 20px;
    box-sizing: border-box;

    span {
      display: inline-block;
      color: #583baf;
     background: rgba(255,255,255, .5);
      position: absolute;
         right: 0;
    top: 10px;
    padding: 5px 12px;
    border-radius: 30px 0 0 30px;
    }

    .bt {
      font-size: 12px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #000;
      margin-bottom: 10px;
    }

    h2 {
      font-size: 30px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: bold;
      color: #fff;
    }

    h4 {
      font-size: 14px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #fff;
    }
  }

  .cent {
    width: 92%;
    margin: 30px auto;
    padding: 20px 0 50px;
    position: relative;
    background: #141634;
    border-radius: 15px;
  }

}
  .btn{
    width: 150px;
    margin: 0 auto;
    display: block;
      background: url(../../assets/img/fan/btn2.png) top center no-repeat;
    background-size: 100% ;
  border: none;
  text-align: center;
  color:#fff;
  font-family: PingFangSC-Medium, PingFang SC;
font-weight: 500;
font-size: 17px;
// border-radius: 10px;
padding:12px 22px;
  }
.my {
     background: url(../../assets/img/fan/min_bg2.png) top center no-repeat;
    background-size: 100% 100%;
  // padding: 30px 20px;
  box-sizing: border-box;
  width: 90%;
  margin: 0 auto 20px;
  position: relative;
  z-index: 2;
  padding: 10px 10px 15px;
span{color: #fff;margin:10px 0 5px;display: block;}
h3{
  font-size:18px;
font-family: DINPro;
font-weight: bold;
color: #02FEF5;
}
}

.list {
  // background: #fff;
  border-radius: 0 0 15px 15px;
  // padding: 20px;
    width: 90%;
  margin: 0 auto 20px;
  box-sizing: border-box;
  .line{
    background: #0C0D1F;
    border-radius: 10px;
    margin:10px 0;
    padding: 5px 10px;
  }
  span {
    display: inline-block;
    padding: 6px 0;
    color: #A6B2DC;
  }

  .textRight span {
    font-size: 15px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 600;
    color: #4da9db;
  }
}
</style>



